<template>
  <v-card>
    <v-navigation-drawer
      v-model="drawer"
      :mini-variant.sync="mini"
      permanent
      app
    >
      <v-list-item class="px-2">
        <v-avatar
              color="info"
              size="35"
              dark
            >
            <span class="white--text headline">{{ text() }}</span>
            </v-avatar>
        <!-- <v-list-item-avatar>
          <v-img src="https://randomuser.me/api/portraits/women/70.jpg"></v-img>
        </v-list-item-avatar> -->
        <v-list-item-title>{{ name }}</v-list-item-title>
        <v-btn
          icon
          @click.stop="mini =!mini"
        >
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
      </v-list-item>
      <template v-slot:append>
        <div class="pa-2">
          <v-btn block color="info" @click="deconnecter()">
            Deconnecter
          </v-btn>
        </div>
      </template>
      <v-divider></v-divider>

      <v-list dense>
        <v-list-item to="/notification"  active-class="active">
          <v-list-item-action>
            <v-icon color="info">mdi-bell-ring</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Notifications</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <!-- if role etudiant show mes demandes-->
        <v-list-item to="/student" v-if="role=='etudiant'" active-class="active">
          <v-list-item-action>
            <v-icon color="info">mdi-cog</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Mes Demandes</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <!-- if role responsable show demandes non gener-->
          <v-list-item to="/responsable" v-if="role=='responsable'" active-class="active">
          <v-list-item-action>
            <v-icon color="info">mdi-cog</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Demandes non gérées</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
          <v-list-item to="/demandeGerer" v-if="role=='responsable'" active-class="active">
          <v-list-item-action>
            <v-icon color="info">mdi-cog-refresh</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Demande gérées</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item to="/crudUser"  v-if="role=='responsable'" active-class="active">
          <v-list-item-action>
            <v-icon color="info">mdi-account-multiple</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Gérer étudiant</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </v-card>
</template>
<script>
  export default {
    name : 'Nav',
    created(){
        this.name=localStorage.nom
        this.role=localStorage.role
    },
    data () {
      return {
        name: '',
        role:'',
        drawer: true,
        items: [
          { title: 'Notification', icon: 'mdi-bell-filled' },
          { title: 'Demande non gérer', icon: 'mdi-cog' },
          { title: 'Demande gérer', icon: 'mdi-cog-refresh' },
        ],
        mini: true,
      }
    },
    methods:{
    deconnecter() {
      this.$router.push({ name: "login" });
      localStorage.clear();
      },
      text(){ 
        return localStorage.nom.substr(0, 1)
        }
    }
  }
</script>